<template>
  <el-cascader ref="cascader" :options="options" @change="change" :props="props" v-on="$listeners" v-bind="$attrs">
    <template slot-scope="{ data, node }">
      <div @click.prevent="selectItem(data, node)">
        <span>{{ data[props.label] }}</span>
        <span v-if="data[props.children] && showLength"> ({{ data[props.children].length }}) </span>
      </div>
    </template>
  </el-cascader>
</template>

<script>
export default {
  name: "ElCascaderNew",
  props: {
    //  1 2  2  3
    maxLevel: {
      type: String,
      default: ""
    },
    delValue: {
      default: ""
    },
    delChildren: {
      type: Boolean,
      default: false
    },
    showLength: {
      type: Boolean,
      default: false
    },
    data: {
      type: Array,
      default(){
        return null
      }
    },
    settings: {
      type: Object,
      default(){
        return {
          level: "level",
          expandTrigger: "hover", //
          multiple: false, //
          checkStrictly: true, // 
          emitPath: false, // false
          lazy: false, // lazyLoad 
          value: "value", //
          label: "label", //
          children: "children", //
          disabled: "disabled", //"
          leaf: "leaf" //
        }
      }
    }
  },
  data() {
    return {
      options: [],
      props: {
        level: "level",
        expandTrigger: "hover", //
        multiple: false, //
        checkStrictly: true, // 
        emitPath: false, // false
        lazy: false, // lazyLoad 
        value: "value", //
        label: "label", //
        children: "children", //
        disabled: "disabled", //
        leaf: "leaf" //
      },
      list: [] //
    }
  },
  watch: {
    data: {
      handler(val){
        //  delValue    bug
        setTimeout(() => {
          // 
          if (!this.delValue) {
            //
            var value = JSON.parse(JSON.stringify(val))
            if (this.maxLevel){ //
              this.limitLevel(value, this.maxLevel)
            }
            if (this.delChildren) { //
              this.clearChildrenField(value)
            }
            this.options = value 
          }
          //
          const options = JSON.parse(JSON.stringify(this.options))
          //
          this.list = this.getChildren(options)
        }, 300)
      },
      immediate: true
    },
    delValue: {
      handler(val){
        if (!val) {
          //bug 
          this.options = JSON.parse(JSON.stringify(this.data))
          return 
        }
        //
        let arr = JSON.parse(JSON.stringify(this.data))
        this.delOneItemById(arr, val)
        arr = this.removeEmptyArrayEle(arr)
        if (this.maxLevel){ //
          this.limitLevel(arr, this.maxLevel)
        }
        if (this.delChildren) {
          this.clearChildrenField(arr)
        }
        this.options = arr 
      },
      immediate: true
    }
  },
  created() {
    Object.assign(this.props, this.settings)
  },
  methods: {
    /**
     * @description: 
     * @param {*}
     * @return {*}
     * @author: syx
     */
    change(){
      if (!this.props.multiple) {
        this.$refs.cascader.dropDownVisible = false
      }
    },
    /**
     * @description: 
     * @param {*} data
     * @param {*} node
     * @return {*}
     * @author: syx
     */
    selectItem(data, node){
      const props = this.props
      // expandTrigger: "hover", //
      // multiple: false, //
      // checkStrictly: true, // 

      //   
      if (props.expandTrigger === "click") {
        //
        if (data[props.children]) {
          return
        } else { //
          this.doneSomeThing(props, data, node)
        }
      } else if (props.expandTrigger === "hover") { //   
        //
        if (data[props.children]) {
          //
          if (!props.checkStrictly) {
            this.doneSomeThing(props, data, node)
          } else { //
            this.doneSomeThing(props, data, node)
          }
        } else { //
          this.doneSomeThing(props, data, node)
        }
      }
    },
    doneSomeThing(props, data, node){
      console.log(node.loaded)
      // 
      if (props.lazy) {
        // 
        if (!node.isLeaf) {
          return
        }
        //
        node.loaded = true
      }

      //
      if (!props.multiple) {
        if (!props.checkStrictly) {
          return
        }
        if (props.emitPath) {
          this.$emit("input", node.path)
          this.$emit("change", node.path)
        } else {
          this.$emit("input", data[props.value])
          this.$emit("change", data[props.value])
        }
        this.$refs.cascader.dropDownVisible = false
      } else { //
        const clickDom = this.judgeClickDOm(event.target)
        clickDom.previousElementSibling.children[0].children[1].click()
      }
    },
    /**
     * @description: 
     * @param {*} dom
     * @return {*}
     * @author: syx
     */
    judgeClickDOm(dom){
      if (dom.className.indexOf("el-cascader-node__label") > -1){
        return dom
      } else {
        return this.judgeClickDOm(dom.parentNode)
      }
    },
    /**
     * @description: 
     * @param {type} 
     * @return {type} 
     * @author: syx
     */
    limitLevel(list, level){
      if (!list) {
        return
      }
      const props = Object.assign(this.props, this.settings)
      for (var i = 0; i < list.length; i++) {
        var item = list[i]
        if (item[props.children] && item[props.children].length > 0) {
          if (item[props.level] === level || item[props.level] === Number(level)){
            delete item[props.children]
          } else {
            this.limitLevel(item[props.children], level)
          }
        }
      }
    },
    /**
     * @description: children
     * @param {type} 
     * @return {type} 
     * @author: syx
     */
    clearChildrenField(list){
      const props = this.props
      if (!list){
        return
      }
      // 
      for (let i = 0;i < list.length;i++) {
        //children 
        if (!list[i][props.children]) {
          continue 
        } else if (list[i][props.children].length > 0) {
          this.clearChildrenField(list[i][props.children])
        } else {
          delete list[i][props.children]
        }
      }
    },
    /**
     * @description: 
     * @param {*} obj
     * @return {*}
     * @author: syx
     */
    getType(obj){
      //tostring
      var toString = Object.prototype.toString
      var map = {
        "[object Boolean]": "boolean", 
        "[object Number]": "number", 
        "[object String]": "string", 
        "[object Function]": "function", 
        "[object Array]": "array", 
        "[object Date]": "date", 
        "[object RegExp]": "regExp", 
        "[object Undefined]": "undefined",
        "[object Null]": "null", 
        "[object Object]": "object"
      }
      if (obj instanceof Element) {
        return "element"
      }
      return map[toString.call(obj)]
    },
    /**
     * @description: 
     * @param {type} item  id 
     * @return {type} 
     * @author: syx
     */    
    delOneItemById(list, delValue){
      const type = this.getType(delValue)
      //created
      const props = Object.assign(this.props, this.settings)
      if ((!list || list.length === 0) || (type === "string" && !delValue) || (type === "array" && delValue && delValue.length === 0)){
        return
      }
      // 
      for (let i = 0;i < list.length;i++) {
        // 
        if ((type === "string" && list[i][props.value] === delValue) || (type === "array" && delValue.includes(list[i][props.value]))) {
          // ""  
          list[i] = ""
        } else {
          // 
          if (list[i][props.children] && list[i][props.children].length > 0) {
            this.delOneItemById(list[i][props.children], delValue)
          }
        }
      }
    },
    /**
     * @description: 
     * @param {*} arr
     * @return {*}
     * @author: syx
     */
    removeEmptyArrayEle(arr){    
      //created
      const props = Object.assign(this.props, this.settings)
      for (var i = 0; i < arr.length; i++) {
        if (!arr[i]) {
          arr.splice(i, 1)
          i = i - 1 // i - 1 , 2 
          // ,
        } else if (arr[i][props.children] && arr[i][props.children].length > 0) {
          arr[i][props.children] = this.removeEmptyArrayEle(arr[i][props.children])
        }
      }
      return arr
    },
    /**
     * @description: 
     * @param {type} 
     * @return {type} 
     * @author: syx
     */
    getChildren(nodes){
      const props = this.props
      var r=[]
      if (Array.isArray(nodes)) {
        for (var i=0, l=nodes.length; i<l; i++) {
          const item = nodes[i]
          r.push(item) // 
          if (Array.isArray(item[props.children])&&item[props.children].length>0){
          // childrenchildren
            r = r.concat(this.getChildren(item[props.children])) 
            item[props.children] = [] //children 
          }
          // delete item[props.children]   children  
        }
      }
      return r
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     * @author: syx
     */
    getDataList() {
      return this.list
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     * @author: syx
     */
    getDataMap() {
      const dataMap = {}
      const props = Object.assign(this.props, this.settings)
      for (let i = 0; i < this.list.length; i++){
        dataMap[this.list[i][props.value]] = this.list[i]
      }
      return dataMap
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     * @author: syx
     */ 
    getCascader() {
      return this.$refs.cascader
    }

  }
}
</script>

<style scoped>
.el-cascader__tags .el-tag>span {
  flex: auto !important;
}
.el-cascader-menu__wrap{
  height: 300px !important;
}
.el-cascader-node__label{
  flex: none !important;
}
.el-cascader-node__postfix, .el-cascader-node__prefix {
  top: 50% !important;
  transform: translateY(-50%) !important;
  line-height: 4px !important;
}
</style>
