<template>
  <el-checkbox-group v-model="svalue" v-on="$listeners" v-bind="$attrs" @change="handdleChange">
    <el-checkbox-button
      v-for="item in list"
      :disabled="item.disabled"
      :key="item[dictSetting.format.value]"
      :label="item[dictSetting.format.value]"
      :border="border"
      :size="size"
      :name="name"
      :true-label="trueLabel"
      :false-label="falseLabel"
      :checked="checked"
      :indeterminate="indeterminate"
    >{{item[dictSetting.format.label]}}</el-checkbox-button>
  </el-checkbox-group>
</template>
<script>
import { state, actions } from "~/store/index.js"
export default {
  name: "el-checkbox-button-dict",
  data() {
    return {
      list: [], //
      svalue: this.value,
      type: "" //
    }
  },
  props: {
    //
    trueLabel: {
      type: [String, Number],
      default: ""
    },
    //
    falseLabel: {
      type: [String, Number],
      default: ""
    },
    //
    border: {
      type: Boolean,
      default: false
    },
    //Checkbox  border  medium / small / mini
    size: {
      type: String,
      default: ""
    },
    // name 
    name: {
      type: String,
      default: ""
    },
    // 
    checked: {
      type: Boolean,
      default: false
    },
    // indeterminate 
    indeterminate: {
      type: Boolean,
      default: false
    },
    //
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //
    value: {}, //v-model
    //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return state.dictCodes
    },
    dictSetting() {
      return state.dictSetting
    },
    isGettingAllDictTypes() {
      return state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return state.isGettingDictTypes
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    },
    //
    value(val) {
      this.svalue = val //propsresultdatamyResult
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val) //myResult
      }
    }
  },
  mounted() {
    // this.svalue=this.value;//
    this.getDictionary_option()
  },
  methods: {
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictSetting.format.value,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      } else {
        dictData = this.dictSetting.filterDataFun(dictData)
      }
      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      } else {
        dictData = this.dictSetting.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictSetting.format.value,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }
      this.list = dictData
    },
    // 
    handdleChange() {
      /* emit
         */
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue)
        return
      }
      this.$emit(
        "dictChange",
        this.list.filter(item => {
          return this.svalue.includes(item[this.dictSetting.format.value])
        })
      )
    }
  }
}
</script>