<template>
  <div class="functions-dict" ref="container">
    <div class="questions-box flex-c-b">
      <div class="question">?</div>
      <div class="answer" v-if="answer1">{{answer1}}</div>
      <el-button type="primary" v-if="!answer1" @click="desensitization('', 'name', 'answer1')"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">18830593872?</div>
      <div class="answer" v-if="answer2">{{answer2}}</div>
      <el-button type="primary" v-if="!answer2" @click="desensitization('18830593872', 'mobile', 'answer2')"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">350624199501051314?</div>
      <div class="answer" v-if="answer3">{{answer3}}</div>
      <el-button type="primary" v-if="!answer3" @click="desensitization('350624199501051314', 'idCard', 'answer3')"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">?</div>
      <div class="answer" v-if="answer4">{{answer4}}</div>
      <el-button type="primary" v-if="!answer4" @click="desensitization('', 'idCard', 'answer4')"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">19950105?</div>
      <div class="answer" v-if="answer5">{{answer5}}</div>
      <el-button type="primary" v-if="!answer5" @click="dayFormat('19950105', 'date', 'answer5')"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">19950105141222?</div>
      <div class="answer" v-if="answer6">{{answer6}}</div>
      <el-button type="primary" v-if="!answer6" @click="dayFormat('19950105141222', 'datetime', 'answer6')"></el-button>
    </div>
    <el-button type="primary" @click="reset"></el-button>

    <div class="flex-c-s flex-col">
      elementmessage
      <el-button type="primary" @click="message('Message')">element</el-button>
      
      <el-button type="primary" @click="message('message')"></el-button>
    </div>

  </div>
</template>

<script>
import {Message} from "element-ui"
import {dayFormat} from "~/index.js"
export default {
  name: "functions-other",
  data() {
    return {
      answer1: "",
      answer2: "",
      answer3: "",
      answer4: "",
      answer5: "",
      answer6: ""
    }
  },
  created() {
    this.$Message = Message
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        const container = this.$refs.container
        const data = {"demoContainerHeight": container.scrollHeight + 20}
        window.parent.postMessage(data, "*")
      }, 1000)
    })
  },
  methods: {
    reset() {
      this.answer1 = this.answer2 = this.answer3 = this.answer4 = this.answer5 = this.answer6 = ""
    },
    //
    desensitization(val, type, answer) {
      this[answer] = this.$desensitization(val, type)
    },
    dayFormat(val, type, answer) {
      this[answer] = dayFormat(val, type)
    },
    message(type){
      this["$" + type].success("vue-element-dict")
    }
  }
}
</script>

<style scoped>
.questions-box{
  margin-bottom: 20px;
}
</style>