/**
 * @description: 
 * @param {*} allApi
 * @return {*}
 * @author: syx
 */
export function cacheApi(allApi, maxApiNum = 10) {
  //
  var cache = {}
  //api
  var cacheArr = []
 
  //
  this.get = function(apiName, uniqueValue, isCache = true, data = {}, config = {}) {
    return new Promise((resolve, reject) => {
      //   
      if (!isCache || !cache[apiName + uniqueValue]) {
        this.set(apiName, data, config).then(res => {
          // 10
          if (cacheArr.length === maxApiNum){
            var _cacheName = cacheArr.shift()
            this.remove(_cacheName)
          }
          cache[apiName + uniqueValue] = res
          cacheArr.push(apiName + uniqueValue)
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      } else { //  
        const _cacheName = apiName + uniqueValue
        const res = cache[_cacheName]
        cacheArr = cacheArr.filter(item => item !== _cacheName)
        cacheArr.push(_cacheName)
        resolve(res)
      }
    })
  },
  /**
   * @description: 
   * @param {*} apiName
   * @param {*} data
   * @param {*} config
   * @return {*}
   * @author: syx
   */  
  this.set = function(apiName, data, config) {
    return allApi[apiName](data, config)
  },
  /**
   * @description: 
   * @param {*} cacheName
   * @return {*}
   * @author: syx
   */  
  this.remove = function(cacheName) {
    delete cache[cacheName]
  },
  /**
   * @description: 
   * @param {*} 
   * @return {*}
   * @author: syx
   */  
  this.removeAll = function() {
    cache = {}
    //api
    cacheArr = []
  }
}