<template>
  <div class="functions-dict" ref="container">
    <div class="questions-box flex-c-b">
      <div class="question">FRUITS1?</div>
      <div class="answer" v-if="answer1">{{answer1}}</div>
      <el-button type="primary" v-if="!answer1" @click="getLabelByCode"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">FRUITS?</div>
      <div class="answer" v-if="answer2">{{answer2}}</div>
      <el-button type="primary" v-if="!answer2" @click="getCodeByLabel"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">FRUITS1,2,3,4?</div>
      <div class="answer" v-if="answer3">{{answer3}}</div>
      <el-button type="primary" v-if="!answer3" @click="getLabelByCodes"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">FRUITS,,?</div>
      <div class="answer" v-if="answer4">{{answer4}}</div>
      <el-button type="primary" v-if="!answer4" @click="getCodeByLabels"></el-button>
    </div>
    <div class="questions-box flex-c-b">
      <div class="question">FRUITS?</div>
      <div class="answer" v-if="answer5.length > 0">
        <div class="flex-c-b" v-for="item in answer5" :key="item.value">
          <div class="value">{{item.value}}</div>
          <div class="label">{{item.label}}</div>
        </div>
      </div>
      <el-button type="primary" v-if="answer5.length === 0" @click="getDictObjByDictTypes1"></el-button>
    </div>

    <div class="questions-box flex-c-b">
      <div class="question">SPORTS,PERSON_TYPE?</div>
      <div class="answer" v-if="answer6.PERSON_TYPE.length > 0">
        SPORTS
        <div class="flex-c-b" v-for="item in answer6.SPORTS" :key="item.value">
          <div class="value">{{item.value}}</div>
          <div class="label">{{item.label}}</div>
        </div>
        PERSON_TYPE
        <div class="flex-c-b" v-for="item in answer6.PERSON_TYPE" :key="item.value">
          <div class="value">{{item.value}}</div>
          <div class="label">{{item.label}}</div>
        </div>
      </div>
      <el-button type="primary"  v-if="answer6.PERSON_TYPE.length === 0" @click="getDictObjByDictTypes2"></el-button>
    </div>

    <el-button type="primary" @click="reset"></el-button>
  </div>
</template>

<script>
import {getCodeByLabel} from "~/index"
const {getCodeByLabels} = require("~/index")
export default {
  name: "functions-dict",
  data() {
    return {
      answer1: "",
      answer2: "",
      answer3: "",
      answer4: "",
      answer5: [],
      answer6: {
        SPORTS: [],
        PERSON_TYPE: []
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        const container = this.$refs.container
        const data = {"demoContainerHeight": container.scrollHeight + 20}
        window.parent.postMessage(data, "*")
      }, 1000)
    })
  },
  methods: {
    reset() {
      this.answer1 = this.answer2 = this.answer3 = this.answer4 = "" 
      this.answer6 = {
        SPORTS: [],
        PERSON_TYPE: []
      }
      this.answer5 = []
    },
    //
    getLabelByCode() {
      this.$getLabelByCode("1", "FRUITS").then(data => {
        this.answer1 = data
      })
    },
    //
    getCodeByLabel() {
      getCodeByLabel("", "FRUITS").then(data => {
        this.answer2 = data
      })
    },
    //
    getLabelByCodes() {
      this.$getLabelByCodes("1,2,23131233,4", "FRUITS", "~~").then(data => {
        this.answer3 = data
      })
    },
    //
    getCodeByLabels() {
      getCodeByLabels(",das,", "FRUITS", "~~").then(data => {
        this.answer4 = data
      })
    },
    getDictObjByDictTypes1() {
      this.$getDictObjByDictTypes("FRUITS").then(data => {
        this.answer5 = data["FRUITS"]
      })
    },
    getDictObjByDictTypes2() {
      this.$getDictObjByDictTypes("SPORTS,PERSON_TYPE").then(data => {
        this.answer6 = data
      })
    }
  }
}
</script>

<style scoped>
.questions-box{
  margin-bottom: 20px;
}
</style>