/**
 * @param {function} func - 
 * @param {number} wait -    1000
 * @param {boolean} immediate -  false
 * @param {number} stopTime -   300
 * @return {function}
 */
export const throttle = (func, wait = 1000, immediate = false, stopTime = 300) => {
  let last, timer
  return function(...arg) {
    const now = +new Date()
    if (last && now < last + wait) {
      clearTimeout(timer)
      timer = setTimeout(function() {
        func.apply(this, arg)
      }, stopTime)
    } else {
      if (!immediate) {
        if (last) {
          func.apply(this, arg)
        }
      } else {
        func.apply(this, arg)
      }
      last = now
    }
  }
}